<template>
	<view class="content">
		<view class="ui-recommend-module">
			<swiper :autoplay="autoplay" @change="changeDots" :circular="circular" class="ui-swiper-wrapper"
				:duration="duration" :interval="interval">
				<swiper-item class="ui-swiper-item">
					<view @tap="getItem(advert.code)" class="cell-item">
						<image class="cell-image" mode="aspectFill" :src="advert.thumbnail"></image>
						<view class="cell-item-mask"></view>
						<view class="cell-content">
							<view class="cell-content-title ">
								<view class="cell-circular cell-orange-circular">
								</view>
								<view class="cell-text font-orange">{{siteInfo.name}}</view>
							</view>
							<view class="cell-content-main">
								<view class="cell-text">
									<view class="cell-text-title">
										{{siteInfo.description}}
									</view>
								</view>
								<navigator class="cell-button" :url="advert.code">
									<button class="" formType="submit">
										立即查看
										<text class="icon-right-arrow"></text>
									</button>
								</navigator>
							</view>
						</view>
					</view>
				</swiper-item>
			</swiper>
			<view class="index-search container-class">
				<view class="search-icon icon-class"></view>
				<input @confirm="onConfirm" @input="onInput" class="search-input input-class" confirmType="search"
					:focus="autoFocus" :placeholder="placeHolder" placeholderClass="search-input-placeholder"
					:value="searchKey" v-if="inputEnable"></input>
				<view class="search-input" v-else>{{placeHolder}}</view>
				<image bindtap="onClear" class="close-btn"
					src="https://cloud-minapp-16269.cloud.ifanrusercontent.com/product-image-close.svg"
					v-if="searchKey"></image>
			</view>
		</view>
		<view class="tab-content">
			<view class="index">
				<view class="card list-card" style="margin-top:26rpx" v-if="stickyPost.length>0">
					<view class="card-header">
						<view class="card-header-label">推荐文章</view>
						<view class="card-header-title">每日精心推荐</view>
					</view>
					<view class="card-content">
						<view class="list">
							<view @click="bindDetail(item.id)" class="list-item" v-for="(item,index) in stickyPost"
								:key="index">
								<view class="item-image">
									<image lazyLoad :src="item.meta.thumbnail"></image>
								</view>
								<view class="item-content">
									<text class="item-title">{{item.title.rendered}}</text>
									<text class="item-des">{{item.excerpt.rendered}}</text>
								</view>
							</view>
						</view>
					</view>
				</view>
				<view class="index_article" v-if="posts.length>0">
					<block v-for="(item,index) in posts" :key="index">
						<block v-if="index%5 !== 0">
							<view @click="bindDetail(item.id)" class="card video-card" style="margin-bottom: 26px">
								<view class="video">
									<image lazyLoad mode="widthFix" :src="item.meta.thumbnail"></image>
								</view>
								<view class="card-header video-card-header">
									<view class="card-header-label">{{item.category[0].name}}</view>
									<view class="card-header-title">{{item.title.rendered}}</view>
									<view class="card-header-subtitle">{{item.excerpt.rendered}}</view>
								</view>
							</view>
						</block>
						<block v-else>
							<view @click="bindDetail(item.id)" class="card image-card white-background"
								v-if="item.format==='standard'" style="margin-bottom: 26px">
								<view class="card-image">
									<image lazyLoad mode="widthFix" :src="item.meta.thumbnail"></image>
								</view>
								<view class="card-header image-card-header">
									<view class="card-header-label wani-label" :style="'color:'+item.style">
										{{item.category[0].name}}
									</view>
									<view class="card-header-title wanzi-title" :style="'color:'+item.style">
										{{item.title.rendered}}
									</view>
								</view>
							</view>
						</block>
					</block>
				</view>
				<view class="last_text" v-if="isLastPage&&posts.length>0">已经到底啦~</view>
				<view class="last_text" v-if="!isLastPage&&posts.length>0">努力加载中...</view>
			</view>
		</view>
	</view>
</template>

<script>
	import API from '@/api/api.js';
	export default {
		data() {
			return {
				isloading: true,
				siteInfo: [],
				advert: [],
				stickyPost: [],
				indicatorDots: !1,
				autoplay: !0,
				interval: 3e3,
				circular: true,
				duration: 300,
				placeHolder: '输入你想知道的内容...',
				autoFocus: false,
				inputEnable: true,
				searchKey: '',
				posts: [],
				page: 1,
				isLastPage: false,
				isBottom: false
			}
		},
		onLoad() {
			this.getSiteInfo();
			this.getAdvert();
			this.getStickyPosts();
			this.getPostList();
		},
		onReachBottom() {
			if (!this.isLastPage && !this.isloading) {
				this.getPostList({
					page: this.page
				})
			}
		},
		onPullDownRefresh() {
			this.page = 1;
			this.posts = [];
			this.isLastPage = false;
			this.getPostList();
			uni.stopPullDownRefresh();
		},
		onShareAppMessage() {
			return {
				title: this.siteInfo.name,
				path:'/pages/index/index',
				desc:this.siteInfo.description,
				imageUrl:this.siteInfo.cover,
			}
		},
		methods: {
			getSiteInfo: function() {
				API.getSiteInfo().then(res => {
						this.siteInfo = res;
						//console.log(res);
					})
					.catch(err => {
						console.log(err)
					})
			},
			getStickyPosts: function() {
				API.getStickyPosts().then(res => {
						this.stickyPost = res
					})
					.catch(err => {
						console.log(err)
					})
			},
			getAdvert: function() {
				API.indexAdsense().then(res => {
						console.log(res)
						if (res.status === 200) {
							this.advert = res.data
						}
					})
					.catch(err => {
						console.log(err)
					})
			},

			getPostList: function(data) {
				API.getPostsList(data).then(res => {
						console.log(res);
						if (res.length < 10) {
							this.isLastPage = true,
								this.loadtext = '到底啦',
								this.showloadmore = false

						}
						this.posts = this.posts.concat(res);
						this.page = this.page + 1;
						this.isloading = false;
						uni.stopPullDownRefresh()
					})
					.catch(err => {
						console.log(err)
						uni.stopPullDownRefresh()
					})
			},
			getItem: function(url) {
				uni.navigateTo({
					url: url
				})

			},
			onInput: function(e) {
				this.searchKey = e.detail.value;

			},

			onConfirm: function() {
				uni.navigateTo({
					url: '/pages/list/list?s=' + this.searchKey,
				})
			},
			bindDetail: function(id) {
				console.log(id);
				uni.navigateTo({
					url: '/pages/detail/detail?id=' + id,
				})
			}
		}
	}
</script>

